.work-page {
	@extend .about-section;
	.workflow {
		@include pos(rel,null);
		padding: 100px 0 0;
		@include media-breakpoint-down(md) {
			padding-top: 0; }
		.work-map {
			text-align: center;
			@include media-breakpoint-down(lg) {
				img {
					width: 70%; } }
			@include media-breakpoint-down(md) {
				display: none;
				img {
					width: 100%; } } }
		.work-list {
			@include media-breakpoint-down(md) {
				@include flexbox;
				flex-wrap: wrap; }
			.work {
				width: 150px;
				@include media-breakpoint-down(lg) {
					width: 130px; }
				@include media-breakpoint-down(md) {
					width: 33.33%;
					padding: 0px 15px;
					margin-bottom: 30px; }
				@include media-breakpoint-down(sm) {
					width: 50%; }
				@include media-breakpoint-down(xs) {
					width: 100%; }
				@include media-breakpoint-up(lg) {
					&:nth-child(1) {
						@include pos(abs, 130px null null 0); }
					&:nth-child(2) {
						@include pos(abs, 0 null null 50%);
						@include transform(translate(-50px,0));
						@include media-breakpoint-down(lg) {
							@include transform(translate(-45px,0)); } }
					&:nth-child(3) {
						@include pos(abs, 130px 0 null null);
						@include media-breakpoint-down(lg) {
							top: 110px; } }
					&:nth-child(4) {
						@include pos(abs, null 0 330px null);
						@include media-breakpoint-down(lg) {
							bottom: 240px; } }
					&:nth-child(5) {
						@include pos(abs, null null 300px 0);
						@include media-breakpoint-down(lg) {
							bottom: 230px; } }
					&:nth-child(6) {
						@include pos(abs, null 250px 60px null);
						@include media-breakpoint-down(lg) {
							right: 200px;
							bottom: 40px; } } }
				figure {
					.work-img {
						text-align: center;
						display: none;
						margin-bottom: 20px;
						@include media-breakpoint-down(md) {
							display: block; } }
					figcaption {
						@include flexbox;
						flex-wrap: wrap;
						align-items: center;
						align-content: center;
						text-align: center;
						@include media-breakpoint-down(md) {
							align-items: flex-start;
							align-content: flex-start;
							text-align: left; }
						.number {
							@include fs(72rem);
							line-height: 0.9;
							font-weight: 900;
							color: $blue;
							width: 100%;
							// width: 70px
							text-align: center;
							@include media-breakpoint-down(lg) {
								@include fs(50rem); }
							@include media-breakpoint-down(md) {
								width: 70px; } }
						.text {
							// width: calc(100% - 70px)
							@include fs(14rem);
							line-height: 1.5;
							@include media-breakpoint-down(md) {
								width: calc(100% - 70px); } } } } } } } }
