// MATERIAL VARIABLE
@mixin flexbox {
	display: flex;
	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flexbox; }
@mixin clearfix {
	content: '';
	display: table;
	width: 100%;
	clear: both; }
@mixin flatbutton {
	display: inline-block;
	@include fs(14rem);
	text-transform: uppercase;
	font-weight: 500;
	font-family: 'Roboto';
	padding: 0 16px;
	line-height: 36px;
	@include bor-rad(2px);
	&:hover {
		background: color-rgba(lighten($black,62%),0.2); }
	&:active {
		background: color-rgba(lighten($black,62%),0.4); }
	&:focus {
		background: color-rgba($black,0.12); } }
@mixin material-card {
	@include bor-rad(2px);
	box-shadow: 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.2), 0 1px 5px 0 rgba(0,0,0,.12); }
@mixin header-sd {
	box-shadow: 0 0 4px rgba(0,0,0,.14), 0 4px 8px rgba(0,0,0,.28); }
@mixin disablescroll {
	&::-webkit-scrollbar-track {
		width: 0;
		height: 0; }
	&::-webkit-scrollbar {
		width: 0;
		height: 0; }
	&::-webkit-scrollbar-thumb {
		border: none;
		width: 0;
		height: 0; } }
@mixin mainbutton {
	line-height: 40px;
	@include bor-rad(50px);
	text-align: center;
	color: $white;
	background: linear-gradient(to right, $green, $green);
	box-shadow: 0px 10px 20px -10px color-rgba($green,1);
	&:hover {
		filter: brightness(105%);
		box-shadow: 0px 10px 20px -5px color-rgba($green,1);
		// +media-breakpoint-up(md)
		// 	filter: unset
		// 	background: linear-gradient(to right, $color1, $color2)
 } }		// 	box-shadow: none

@mixin fs($size) {
	font-size: ($size / 16); }
body {
	margin: 0;
	padding: 0;
	@include fs(15rem);
	font-family: 'Montserrat', sans-serif;
	color: $main;
	line-height: 1.5;
	// background: $grey
	overflow: hidden;
	@include media-breakpoint-down(lg) {
		@include fs(14rem); }
	@include media-breakpoint-down(sm) {
		@include fs(13rem); } }

html {
	overflow: auto; }
.fullpage {
	// overflow: hidden
	max-width: 1600px;
	margin: 0 auto; }
a {
	@include transition(all,0.5s); }
a, a:hover {
	&:focus, &:active, &:hover {
		outline: none;
		text-decoration: none; } }
input,button, select, textarea {
	@include transition(all,0.3s);
	&:hover, &:active, &:focus {
		outline: none; } }
button {
	border: none;
	background: none;
	margin: 0;
	padding: 0;
	@include transition(all,0.3s); }
img, iframe {
	vertical-align: middle; }
main {
	clear: both;
	&:before, &:after {
		content: '';
		display: table;
		width: 100%;
		clear: both; } }

strong {
	span {
		font-weight: bold; } }
.container {
	@include media-breakpoint-up(xl) {
		max-width: 1170px;
		width: 100%; } }
.content {
	line-height: 1.5;
	@include fs(14rem);
	+ .share {
		margin: 15px 0; }
	@include media-breakpoint-down(md) {
		@include fs(13rem);
		line-height: 1.8; }
	@include media-breakpoint-down(xs) {
		@include fs(12rem); }
	ul, ol, p, h1, h2, h3, h4, h5, h6, img, figure {
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0; }
		@include media-breakpoint-down(xs) {
			margin-bottom: 10px; } }
	figure {
		margin: 0 auto 15px;
		text-align: center;
		@include media-breakpoint-down(xs) {
			margin-bottom: 10px; }
		img {
			margin-bottom: 5px; }
		figcaption {
			// background: darken($white,5%)
			padding: 5px 0;
			text-align: center;
			@include fs(13rem);
			font-style: italic;
			color: darken($white,60%); } }
	ul {
		padding-left: 25px;
		list-style-type: disc; } }

