.banner-page {
	@include pos(rel,null);
	@include media-breakpoint-down(md) {
		padding: 30px 0; }
	@include media-breakpoint-down(xs) {
		padding: 15px 0; }
	.banner-img {
		@include media-breakpoint-down(md) {
			@include pos(abs, 0 null 0 0);
			right: 0; }
		img {
			width: 100%;
			@include media-breakpoint-down(md) {
				height: 100%;
				object-fit: cover; } } }
	.page-intro {
		background: color-rgba($white,0.3);
		padding: 30px 35px;
		color: $main;
		line-height: 1.3;
		text-shadow: 0 1px 2px color-rgba($white,1);
		@include pos(abs, 50% null null auto);
		@include transform(translate(0,-50%));
		width: 500px;
		z-index: 5;
		@include media-breakpoint-down(md) {
			@include pos(rel,null);
			z-index: 5;
			@include transform(translate(0,0)); }
		@include media-breakpoint-down(sm) {
			width: 100%;
			background: color-rgba($white,0.5); }
		@include media-breakpoint-down(xs) {
			padding: 20px; }
		.page-name {
			@include fs(24rem);
			font-weight: 700;
			text-transform: uppercase;
			margin-bottom: 15px;
			@include media-breakpoint-down(lg) {
				@include fs(20rem); }
			@include media-breakpoint-down(sm) {
				@include fs(18rem); } }
		.intro-content {
			@include fs(15rem);
			font-weight: 500;
			@include media-breakpoint-down(lg) {
				@include fs(14rem); } } } }
