.pages {
	.pagination {
		@include flexbox;
		justify-content: center;
		li {
			+ li {
				margin-left: 10px; }
			a, span {
				display: block;
				width: 40px;
				height: 40px;
				line-height: 40px;
				text-align: center;
				@include bor-rad(5px);
				color: $main;
				font-weight: normal;
				background: darken($white,5%); }
			&.active {
				a, span {
					background: $green;
					color: $white; } } } } }
.product-category {
	margin-bottom: 40px;
	ul.category-link {
		> li {
			@include pos(rel,null);
			margin-bottom: 1px;
			> a {
				display: block;
				color: $main;
				background: darken($white,5%);
				padding: 9px 30px;
				@include fs(15rem); }
			.btn-showsub {
				@include pos(abs, 7px 15px null null);
				@include fs(18rem);
				cursor: pointer;
				@include transition(all,0.5s); }
			.sub {
				display: none;
				border: 1px solid darken($white,5%);
				border-top: none;
				ul {
					> li {
						border-bottom: 1px solid darken($white,10%);
						&:last-child {
							border-bottom: none; }
						> a {
							display: block;
							color: $main;
							padding: 9px 30px;
							padding-left: 50px;
							@include fs(15rem);
							@include pos(rel,null);
							&:before {
								content: '';
								display: block;
								width: 5px;
								height: 5px;
								background: $main;
								@include pos(abs, 20px null null 30px); } }
						&:hover {
							> a {
								color: $green; } }
						&.active {
							> a {
								color: $green;
								font-weight: 500;
								border-left: 4px solid $green;
								margin-left: -1px;
								padding-left: 46px;
								&:before {
									background: $green;
									left: 26px; } } } } } }
			&.active {
				> a {
					color: $white;
					background: $green; }
				.btn-showsub {
					color: $white;
 }					// +transform(scale(1,-1))
				.sub {
 } } } } }					// display: block
.product-page {
	@extend .about-section;
	.product-slide {
		margin: 40px 0 60px;
		.img {
			@include flexbox;
			justify-content: center;
			align-items: center;
			height: 280px;
			img {
				max-width: 100%;
				max-height: 100%; } } }
	.product-info {
		margin: 40px 0;
		.product-name {
			@include fs(24rem);
			line-height: 1.5;
			font-weight: 600;
			text-transform: uppercase;
			color: $blue;
			margin-bottom: 20px; }
		.product-overview {
			@include fs(14rem);
			font-weight: 300; } }
	.product-description {
		margin-bottom: 40px;
		.product-table {
			// max-width: 850px
			width: 100%;
			margin: 0 auto;
			border: 1px solid darken($white,10%);
			th, td {
				padding: 10px 45px; }
			th {
				@include fs(16rem);
				font-weight: 500;
				background: darken($white,20%);
				color: $main; }
			td {
				padding: 10px 45px;
				+ td {
					border-left: 1px solid darken($white,10%);
					max-width: 200px; } }
			tr {
				&:nth-child(even) {
					td {
						background: darken($white,3%); } } } } }
	.category-list {
		.category {
			margin-bottom: 40px;
			figure {
				.category-img {
					@include flexbox;
					height: 240px;
					margin-bottom: 30px;
					justify-content: center;
					align-items: flex-end;
					align-content: flex-end;
					background: url(../img/feed/prbg.png) center top no-repeat;
					img {
						@include transform(scale3d(0.9,0.9,1));
						@include transition(all,0.5s); } }
				figcaption {
					.category-name {
						text-align: center;
						a {
							display: inline-block;
							@include bor-rad(50px);
							border: 1px solid $green;
							padding: 10px 20px;
							@include fs(14rem);
							font-weight: 600;
							text-transform: uppercase;
							color: $green; } } }
				&:hover {
					.category-img {
						img {
							@include transform(scale3d(1,1,1)); } }
					figcaption {
						.category-name {
							a {
								background: linear-gradient(to right, $blue, $green);
								color: $white;
								box-shadow: 0px 10px 20px -10px color-rgba($blue,1); } } } } } } } }
.product-list {
	margin-bottom: 30px;
	.product-table {
		border: 1px solid darken($white,10%);
		width: 100%;
		th, td {
			border: none;
			padding: 10px;
			border-left: 1px solid darken($white,10%);
			&:first-child {
				border-left: none; }
			&:first-child, &:last-child {
				text-align: center; } }
		th {
			font-weight: 500;
			color: $white;
			background: $green; }
		td {
			@include fs(14rem); }
		a {
			color: $blue;
			font-weight: 500;
			&:hover {
				color: $green; } }
		tr {
			&:nth-child(odd) {
				background: darken($white,3%); } } }
	.product-col {
		margin-bottom: 30px;
		figure {
			.product-img {
				@include flexbox;
				justify-content: center;
				align-items: center;
				height: 240px;
				margin-bottom: 10px;
				img {
					max-width: 90%;
					max-height: 90%; } }
			figcaption {
				.product-name {
					text-align: center;
					a {
						@include fs(14rem);
						font-weight: 300;
						color: $main; } } } } } }
.brand-list {
	.brand-col {
		margin-bottom: 30px;
		figure {
			border: 1px solid darken($white,10%);
			.brand-img {
				@include flexbox;
				justify-content: center;
				align-items: center;
				height: 170px;
				img {
					max-width: 80%;
					max-height: 80%;
					@include transition(all,0.5s); } }
			figcaption {
				background: darken($white,10%);
				.brand-name {
					text-align: center;
					@include fs(15rem);
					a {
						display: block;
						padding: 10px 0;
						color: $main; } } }
			&:hover {
				.brand-img {
					img {
						@include transform(scale3d(1.1,1.1,1)); } }
				figcaption {
					.brand-name {
						font-weight: 600;
						a {
							color: $green; } } } } } } }
