.banner-wrap {
	.banner {
		&:after {
			content: '';
			display: block;
			width: 50px;
			height: 48px;
			background: url(../img/titleicon.png) center center no-repeat;
			@include pos(abs, null null -26px 50%);
			@include transform(translateX(-50%));
			z-index: 10; }
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			@include fs(0);
			z-index: 5;
			opacity: 0;
			color: $white;
			text-shadow: 0px 10px 30px color-rgba($black,0.3);
			&:before {
				content: '';
				font-family: 'fontawesome';
				@include fs(42rem); }
			&:hover {
				opacity: 1; } }
		&:hover {
			.slick-prev, .slick-next {
				opacity: 0.7;
				&:hover {
					opacity: 1; } } }
		.slick-prev {
			left: 50px;
			&:before {
				content: '\f104'; } }
		.slick-next {
			right: 50px;
			&:before {
				content: '\f105'; } }
		.slick-dots {
			@include pos(abs, null 0 30px 0);
			text-align: center;
			li {
				display: inline-block;
				width: 10px;
				height: 10px;
				@include bor-rad(50px);
				background: color-rgba($white,1);
				opacity: 0;
				+ li {
					margin-left: 10px; }
				&.slick-active {
					background: $blue; } } }
		.item {
			@include pos(rel,null); }
		.banner-img {
			// +pos(abs, 0 null 0 0)
			// right: 0
			// +flexbox
			// justify-content: center
			// align-items: center
			img {
				width: 100%;
				// height: 100%
				// object-fit: cover
				@include media-breakpoint-down(xs) {
					height: 250px;
					object-fit: cover; } } }
		.banner-caption {
			background: color-rgba($white,0.3);
			padding: 30px 35px;
			color: $main;
			line-height: 1.3;
			text-shadow: 0 1px 2px color-rgba($white,1);
			@include pos(abs, 50% null null auto);
			@include transform(translate(0,-50%));
			width: 550px;
			z-index: 5;
			@include media-breakpoint-down(lg) {
				width: 50%; }
			@include media-breakpoint-down(md) {
				width: 60%; }
			@include media-breakpoint-down(sm) {
				width: calc(100% - 30px); }
			@include media-breakpoint-down(xs) {
				top: unset;
				bottom: 30px;
				padding: 10px; }
			.big {
				@include fs(24rem);
				font-weight: 800;
				text-transform: uppercase;
				margin-bottom: 15px;
				@include media-breakpoint-down(lg) {
					@include fs(24rem); }
				@include media-breakpoint-down(md) {
					@include fs(20rem); }
				@include media-breakpoint-down(sm) {
					@include fs(18rem); }
				@include media-breakpoint-down(xs) {
					@include fs(14rem); } }
			.des {
				@include fs(15rem);
				font-weight: 700;
				@include pos(rel,null);
				z-index: 5;
				@include media-breakpoint-down(lg) {
					@include fs(13rem); }
				@include media-breakpoint-down(sm) {
					@include fs(12rem); }
				@include media-breakpoint-down(xs) {
					@include fs(12rem); } } } } }
