
.top-nav {
	background: $white;
	border-bottom: 1px solid darken($white,10%);
	margin-bottom: -1px;
	ul.top-link {
		@include flexbox;
		justify-content: center;
		padding: 10px 0;
		@include media-breakpoint-down(md) {
			display: block;
			white-space: nowrap;
			padding: 5px 15px;
			overflow: auto; }
		li {
			@include media-breakpoint-down(md) {
				display: inline-block; }
			a {
				display: block;
				line-height: 28px;
				@include fs(14rem);
				text-transform: uppercase;
				text-align: center;
				font-weight: 600;
				color: $main;
				border: 1px solid transparent;
				padding: 0px 20px;
				@include bor-rad(50px);
				@include media-breakpoint-down(md) {
					@include fs(12rem); }
				&.mPS2id-highlight {
					border-color: $green;
					color: $green; } } } } }
.about-title {
	@include fs(24rem);
	font-weight: 700;
	color: $blue;
	text-transform: uppercase;
	margin-bottom: 30px;
	&:after {
		content: '';
		display: block;
		width: 60px;
		height: 1px;
		background: $blue;
		margin-top: 10px; }
	&.text-center {
		&:after {
			margin: 10px auto 0; } } }
.about-stitle {
	@include fs(24rem);
	line-height: 1.3;
	font-weight: 500;
	color: $main;
	margin-bottom: 20px; }
.about-section {
	border-top: 1px solid darken($white,10%);
	background: url(../img/corp/aboutbg.png) top left repeat-x;
	padding: 50px 0;
	overflow: hidden;
	@include pos(rel,null); }
.about-intro {
	.intro-img {
		@include pos(abs, null -250px 0 null);
		z-index: 1;
		@include media-breakpoint-down(lg) {
			width: 80%;
			right: -150px; }
		@include media-breakpoint-down(md);
		@include media-breakpoint-down(xs) {
			right: -100px; } }
	.intro-feature {
		margin-bottom: 50px;
		.feature {
			margin-bottom: 30px;
			figure {
				.icon {
					@include flexbox;
					height: 80px;
					justify-content: center;
					align-items: center; }
				figcaption {
					padding: 20px 0 0;
					text-align: center;
					&:after {
						content: '';
						display: block;
						width: 50px;
						height: 1px;
						background: $green;
						margin: 0 auto; }
					.number {
						color: $blue;
						@include fs(36rem);
						margin-bottom: 5px;
						strong {
							display: inline-block;
							font-weight: 700;
							line-height: 1; }
						span {
							display: inline-block;
							margin-left: 5px;
							@include fs(22rem);
							font-weight: 500; } }
					.name {
						@include fs(16rem);
						text-transform: uppercase;
						line-height: 1.5;
						margin-bottom: 20px;
						color: darken($white,60%);
						@include media-breakpoint-down(lg) {
							@include fs(12rem); } } } } } }
	.intro-content {
		padding-bottom: 100px;
		@include media-breakpoint-down(md) {
			padding-bottom: 250px; }
		@include media-breakpoint-down(xs) {
			padding-bottom: 150px; }
		.intro-title {
			@include fs(30rem);
			font-weight: 300;
			line-height: 1.3;
			color: $blue;
			margin-bottom: 25px;
			@include media-breakpoint-down(md) {
				@include fs(24rem); }
			@include media-breakpoint-down(sm) {
				@include fs(18rem); } }
		.content {
			font-weight: 300; } } }


.about-service {
	padding-bottom: 0;
	.service-list {
		.service-col {
			.service-img {
				.slick-dots {
					@include pos(abs, null 0 20px 0);
					text-align: center;
					@include media-breakpoint-down(lg) {
						bottom: 10px; }
					li {
						display: inline-block;
						width: 10px;
						height: 10px;
						@include bor-rad(50px);
						background: $green;
						&.slick-active {
							background: $white; }
						+ li {
							margin-left: 10px; }
						button {
							display: none; } } }
				.item {
					figure {
						@include pos(rel,null);
						&:before {
							content: '';
							@include pos(abs, 0 null 0 0);
							right: 0;
							background: #00224d;
							opacity: 0.25; }
						img {
							width: 100%; }
						figcaption {
							@include pos(abs, null 0 65px 0);
							z-index: 3;
							text-align: center;
							padding: 0px 120px;
							color: $white;
							@include media-breakpoint-down(lg) {
								padding: 0px 50px;
								bottom: 30px; }
							@include media-breakpoint-down(md) {
								padding: 0px 30px; }
							@include media-breakpoint-down(sm) {
								padding: 0px 15px; }
							.service-name {
								@include fs(24rem);
								margin-bottom: 15px;
								@include media-breakpoint-down(md) {
									@include fs(18rem);
									font-weight: 600; } }
							.service-des {
								@include fs(14rem);
								@include media-breakpoint-down(md) {
									@include fs(13rem); } } } } } } } } }

.about-improving {
	padding-bottom: 0;
	.improve-list {
		article {
			&:nth-child(odd) {
				@include media-breakpoint-up(lg) {
					.improve-col {
						figure {
							.improve-img {
								order: 2; }
							figcaption {
								order: 1;
								&:before {
									top: inherit;
									bottom: 0;
									@include transform(translate(-50%,50%)rotateZ(45deg)); } } } } } } }
		.improve-col {
			figure {
				@include flexbox;
				flex-wrap: wrap;
				.improve-img {
					width: 100%;
					.slick-dots {
						@include pos(abs, null 0 10px 0);
						text-align: center;
						li {
							display: inline-block;
							width: 10px;
							height: 10px;
							@include bor-rad(50px);
							background: $green;
							&.slick-active {
								background: $white; }
							+ li {
								margin-left: 10px; }
							button {
								display: none; } } }
					img {
						width: 100%; } }
				figcaption {
					width: 100%;
					height: 320px;
					@include flexbox;
					flex-wrap: wrap;
					justify-content: center;
					align-items: center;
					align-content: center;
					background: darken($white,4%);
					padding: 0px 70px;
					@include pos(rel,null);
					@include media-breakpoint-down(lg) {
						padding: 0px 20px;
						height: 270px; }
					@include media-breakpoint-down(md) {
						height: inherit;
						padding: 30px 40px; }
					@include media-breakpoint-down(sm) {
						padding: 20px; }
					&:before {
						content: '';
						display: block;
						width: 20px;
						height: 20px;
						background: darken($white,4%);
						@include pos(abs, 0 null null 70px);
						@include transform(translate(-50%,-50%)rotateZ(45deg)); }
					.improve-name {
						width: 100%;
						@include fs(24rem);
						font-weight: 600;
						color: $main;
						margin-bottom: 10px;
						@include media-breakpoint-down(lg) {
							@include fs(18rem); }
						@include media-breakpoint-down(sm) {
							@include fs(16rem); } }
					.improve-des {
						width: 100%;
						@include fs(14rem);
						// font-weight: 300
						line-height: 1.6;
						@include media-breakpoint-down(lg) {
							@include fs(13rem); } } } } } } }
.about-quality {
	padding: 70px 0;
	.quality-banner {
		margin-bottom: 40px; }
	.cert-list {
		.slick-prev, .slick-next {
			@include pos(abs, calc(50% - 40px) null null null);
			@include transform(translateY(-50%));
			@include fs(0);
			z-index: 5;
			opacity: 0.3;
			&:before {
				content: '';
				@include fs(36rem); }
			&:hover {
				opacity: 1; } }
		.slick-prev {
			left: 0;
			&:before {
				content: '\f104'; } }
		.slick-next {
			right: 0;
			&:before {
				content: '\f105'; } }
		.item {
			.cert {
				.cert-img {
					@include flexbox;
					justify-content: center;
					align-items: center;
					height: 240px;
					@include media-breakpoint-down(lg) {
						height: 170px; }
					img {
						max-width: 100%;
						max-height: 100%; } }
				figcaption {
					padding: 20px 0; }
				.cert-name {
					text-align: center;
					padding: 0px 20px;
					@include fs(14rem);
					line-height: 1.4;
					font-weight: 600;
					color: $main;
					@include media-breakpoint-down(lg) {
						@include fs(13rem);
						font-weight: 500; } } } } } }
