.contact-page {
	@extend .about-section;
	.contact-form {
		margin-bottom: 60px;
		@include media-breakpoint-down(md) {
			margin-bottom: 40px; }
		.form-group {
			margin-bottom: 10px;
			label {
				display: none; }
			input, textarea {
				background: darken($white,4%);
				border: 1px solid darken($white,10%);
				padding: 10px 20px;
				@include fs(14rem);
				color: darken($white,60%);
				@include media-breakpoint-down(lg) {
					@include fs(13rem); }
				&:hover, &:active, &:focus {
					background: $white; } }
			textarea {
				min-height: 155px; } }
		input[type="submit"] {
			background: $green;
			color: $white;
			padding: 10px 50px;
			text-transform: uppercase;
			border: none;
			@include media-breakpoint-down(lg) {
				@include fs(13rem); } } }
	.contact-info {
		background: #e8eff7;
		padding: 25px 30px;
		margin-bottom: 60px;
		@include media-breakpoint-down(lg) {
			padding: 20px; }
		@include media-breakpoint-down(md) {
			margin-bottom: 40px; }
		.title {
			@include fs(24rem);
			font-weight: 600;
			color: $main;
			margin-bottom: 10px;
			@include media-breakpoint-down(lg) {
				@include fs(18rem); } }
		ul {
			li {
				@include pos(rel,null);
				padding-left: 25px;
				margin-bottom: 12px;
				@include fs(14rem);
				@include media-breakpoint-down(lg) {
					@include fs(13rem); }
				.fa {
					@include pos(abs, 2px null null 0);
					color: $blue;
					@include fs(16rem); }
				a {
					color: $main;
					&:hover {
						color: $blue; } } } } }
	.contact-map {
		iframe {
			@include media-breakpoint-down(sm) {
				height: 400px; }
			@include media-breakpoint-down(xs) {
				height: 300px; } } } }
